import { css } from '@emotion/css';
import { headerMenuHeightPX, mobileMediaQuery } from 'components';
import { Colors } from '../colors';
import { fadeInKeyFrames } from '../consts';

const openedPadding = 30;
const closedPadding = 20;
const desktopHeight = 509;

export const bottomMenuContainerStyle = (isOpened: boolean, initialHeight: number): string => css({
  position: 'fixed',
  width: '100%',
  height: isOpened ? desktopHeight + openedPadding * 2 : initialHeight + closedPadding * 2,
  transition: 'height 200ms ease-in',
  bottom: 0,
  left: 0,
  display: 'flex',
  justifyContent: 'center',

  [mobileMediaQuery]: {
    height: isOpened ? `calc(100% - ${headerMenuHeightPX + openedPadding * 2}px)` : initialHeight + closedPadding * 2
  }
});

export const bottomMenuStyle = (isOpened: boolean): string => css({
  width: isOpened ? '491px' : '223px',
  height: '100%',
  cursor: isOpened ? 'default' : 'pointer',
  backgroundColor: Colors.BLACK,
  transition: 'width 200ms ease-in',
  padding: isOpened ? openedPadding : '20px 20px',
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'space-between',
  alignItems: 'center',
  boxSizing: 'border-box',

  [mobileMediaQuery]: {
    width: isOpened ? '100%' : '223px'
  }
});

export const openedMenuStyle = css({
  opacity: 0,
  animation: `${fadeInKeyFrames} 200ms ease-in forwards`

});
