import { css } from '@emotion/css';
import { mobileMediaQuery } from 'components';
import { generalMenuWidthPX, sheetWidthPX, fadeInKeyFrames } from '../consts';

export const headerStyle = (isMenuOpened: boolean, withMenu: boolean): string => css({
  position: 'fixed',
  width: '100%',
  height: isMenuOpened ? '100%' : withMenu ? '118px' : undefined,
  transition: 'height 200ms ease-in',
  top: 0,
  left: 0,
  display: 'flex',
  justifyContent: 'center',
  zIndex: 10
});

export const headerMenuStyle = (isMenuOpened: boolean): string => css({
  backgroundColor: '#fff',
  width: isMenuOpened ? sheetWidthPX : generalMenuWidthPX,
  height: '100%',
  transition: 'width 200ms ease-in',
  boxSizing: 'border-box',
  display: 'flex',
  justifyContent: 'space-between',
  flexDirection: 'column',
  padding: '20px',

  [mobileMediaQuery]: {
    width: isMenuOpened ? '100vw' : generalMenuWidthPX
  }
});

export const menuItemsStyle = css({
  opacity: 0,
  animation: `${fadeInKeyFrames} 200ms ease-in 300ms forwards`
});
